body {
  background-image: url("./img/background.png");
  min-width: 320px;
}

.main {
  max-width: 1192px;
  margin: 0 auto;
}

.loader {
  position: fixed;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.9);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  transition: all 0.1s linear;
}
.guest {
  display: flex;
  height: 100vh;
  align-items: center;
  max-width: 100%;
}
